html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-family: "Nunito", sans-serif;
  font-size: 100%;
  vertical-align: baseline;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote::before,
blockquote::after,
q::before,
q::after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

.topMargin {
  margin-top: 1em;
}

.leftMargin {
  margin-left: 1em;
}

.halfLeftMargin {
  margin-left: 0.5em;
}

.rightMargin {
  margin-right: 1em;
}

.halfRightMargin {
  margin-right: 0.5em;
}

.marginMiddle {
  margin-left: auto;
  margin-right: auto;
}

.flexRow {
  display: flex;
  flex-direction: row;
}

.flexColumn {
  display: flex;
  flex-direction: column;
}

.flexMainCenter {
  justify-content: center;
}

.flexCrossCenter {
  align-items: center;
}

.flexEnd {
  justify-content: flex-end;
}

.flexChange {
  flex: 1 1 auto;
}

.flexNoChange {
  flex: 0 0 auto;
}

.centerText {
  vertical-align: middle;
}

.inlineBlock {
  display: inline-block;
}

h1 {
  font-size: 4em;
}

.lightBlue {
  background-color: #6aceff;
}

.mediumBlue {
  background-color: #12a3eb;
}

.darkBlue {
  background-color: #0076b1;
}

.lightWhite {
  background-color: #ecf3f7;
}

.whiteText {
  color: #ffffff;
}

.redText {
  color: #d31141;
}

.smallText {
  font-size: 10px;
}

.upper {
  text-transform: uppercase;
}

.underline {
  text-decoration: underline;
}

.blueLink {
  color: #105289;
  text-decoration: underline;
  cursor: pointer;
}

.roundBorder {
  padding: 5px;
  border-radius: 7px;
}

.main {
  max-width: 1200px;
}

.forumTitle {
  font-family: "Trebuchet MS", Helvetica, Arial, Sans-serif;
  font-size: 1.2em;
  font-weight: bold;
  text-decoration: none;
}

.oneForum {
  min-height: 50px;
}

.pageBox {
  border: 1px solid black;
  background-color: #e9e9e9;
}

.postBox {
  display: grid;
  grid-template-columns: 3fr 1fr;
  width: auto;
}

a {
  color: #105289;
}

button {
  display: inline-block;
  background-color: #e9e9e9;
  padding: 2px 8px;
  font-size: 13px;
  font-weight: 600;
  font-family: "Open Sans", "Droid Sans", Verdana, Arial, Helvetica, sans-serif;
  line-height: 1.4;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  cursor: pointer;
  -webkit-user-select: none;
  user-select: none;
  border: 1px solid gray;
  border-radius: 4px;
}

.userProfile {
  width: 200px;
  border-left: 1px solid white;
  flex-shrink: 0;
}

img {
  width: 20px;
  height: 20px;
}

.bigInputBox {
  width: 1100px;
  height: 5em;
  overflow-y: scroll;
}

